.landing {
  background-image: url('../../imgs/landing_background.jpg');
  width: 100%;
  min-height: 100vh;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
}

.landing-text {
  text-align: center;
  color: white;
  font-size: 32pt;
  line-height: 1.3;
  pointer-events: none;
  font-family: Raleway;
}

.button {
  background: rgba(40, 44, 52, 0);
  color: white;
  font-size: 16pt;
  outline: none;
  transition: all .5s;
  cursor: pointer;
  border: 2px solid #fff;
  padding: 10px;
  margin-top: 30px;
  display: flex;
  align-self: center;
}

.landing-name {
  color: var(--pink);
}

.button .right-icon {
  align-self: center;
  font-size: 14pt;
  transition: all .5s;
}

.button:hover {
  color: #fff;
  background-color: var(--aqua);
  border-color: var(--aqua);
  transition: all .5s;
}

.button:hover .right-icon {
  transition: all .2s;
  transform: rotate(90deg);
}

.button-text {
  padding-right: 10px;
}

@media screen and (max-width: 480px){
  .landing-text {
    font-size: 16pt;
  }
  .button {
    font-size: 12pt;
    padding: 10px;
    margin-top: 20px;
  }
  .button .right-icon {
    font-size: 10pt;
  }
}
